import React from 'react';
import { TextField } from '@material-ui/core'
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import  Typography  from '@material-ui/core/Typography';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
     display: 'flex',
     justifyContent: 'center',
     flexDirection: "column"
    },
    text: {

    },
    question: {
        fontSize: '22px'
    },

    markedText: {
        margin: 0
    },

    breakText: {
        paddingBottom: '4px',
        borderBottom: '2px solid #cecece'
    }
  }),
);

export default function DaySeven(props: any) {
    const classes = useStyles();
    const [input, setInput] = React.useState('')
    const [score, setScore] = React.useState(1)

    React.useEffect(() => {
        let i = input.toLocaleLowerCase().trim()
        if(i === 'carrinho de bebê' || i === 'carrinho de bebe') { 
            props.setAnswer(true)
        }
    }, [input])


    return (
        <div className={classes.root}>
            <Typography className={classes.text}>
                <p className={classes.breakText}>
                    <strong>
                        A minha história. <br />
                        Estávamos indo para a igreja todos contentes. Mas sem percebermos você levou seu primeiro tombo. <br />
                        De onde vc caiu? Kkkkk <br />
                    </strong>
                </p>
            </Typography>
            <TextField 
                onChange={e => setInput(e.target.value)}
                label="Resposta"
                variant="filled"
                error={props.error}
                helperText={props.error ? `Você está ${((1 - score) * 100).toFixed(2)}% perto da resposta` : ''}
            >
            </TextField>

        </div>
    )   
}
